<template>
    <div>
        <el-table :height="300"
                  :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
                  style="width: 100%">
            <el-table-column
                label="编号"
                width="180">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.id }}</span>
                </template>
            </el-table-column>
            <el-table-column
                label="姓名"
                width="180">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>姓名: {{ scope.row.name }}</p>
                        <p>住址: {{ scope.row.address }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.name }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="生日" prop="bir"></el-table-column>
            <el-table-column label="性别" prop="sex"></el-table-column>
            <el-table-column label="地址" prop="address"></el-table-column>
            <el-table-column align="right">
                <!--搜索框-->
                <template slot="header" slot-scope="scope">
                    <el-input
                        v-model="search"
                        size="mini"
                        placeholder="输入关键字搜索"/>
                </template>
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑
                    </el-button>
                    <el-popconfirm
                        confirmButtonText='好的'
                        cancelButtonText='不用了'
                        icon="el-icon-info"
                        iconColor="red"
                        title="确定删除吗？"
                        @onConfirm="handleDelete(scope.$index, scope.row)"
                    >
                        <el-button size="mini"
                                   type="danger"
                                   slot="reference">删除
                        </el-button>
                    </el-popconfirm>
                </template>
            </el-table-column>
        </el-table>


        <el-row>
            <el-col :span="12" :offset="11">
                <!--分页组件-->
                <el-pagination style="margin: 10px 0px;"
                               layout="prev, pager, next, jumper, total, sizes"
                               :total="total"
                               :page-sizes="[2, 4, 6, 8]"
                               :page-size="size"
                               :current-page="pageNow"
                               prev-text="上一页"
                               next-text="下一页"
                               background
                               @current-change="findPage"
                @size-change="findSize">
                    <!--总记录数，每页显示条数（要配合数组）-->
                </el-pagination>
            </el-col>
        </el-row>

        <el-button style="margin: 15px 0px;" @click="saveUserInfo" type="success" size="mini">添加</el-button>

        <!--添加表单-->
        <transition name="el-zoom-in-center">
            <div v-show="show" class="transition-box">
                <!--:hide-required-asterisk="false"展示星号
                    :rules="rules"验证规则
                    ref="userForm"别称
                -->
                <el-form :hide-required-asterisk="false" :rules="rules" ref="userForm" :model="form" label-suffix=":"
                         label-width="80px">
                    <el-form-item label="姓名" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="生日" prop="bir">
                        <!--日期问题：需要添加value-format="yyyy-MM-dd"-->
                        <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="选择日期" v-model="form.bir"
                                        style="width: 100%;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="性别">
                        <el-radio-group v-model="form.sex">
                            <el-radio label="男"></el-radio>
                            <el-radio label="女"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="详细地址" prop="address">
                        <el-input type="textarea" v-model="form.address"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit('userForm')">保存信息</el-button>
                        <el-button @click="saveUserInfo">重置</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "List",
        data() {
            return {
                tableData: [],
                search: '',
                show: false,
                form: {
                    name: '',
                    bir: '',
                    sex: '男',
                    address: '',
                },
                size: 4,
                pageNow: 1,
                total: 0,
                rules: {
                    name: [
                        {required: true, message: '请输入用户姓名', trigger: 'blur'},
                    ],
                    bir: [
                        {required: true, message: '请选择用户生日', trigger: 'blur'}
                    ],
                    address: [
                        {required: true, message: '请选择用户地址', trigger: 'blur'}
                    ],
                    date1: [
                        {type: 'date', required: true, message: '请选择日期', trigger: 'change'}
                    ],
                    date2: [
                        {type: 'date', required: true, message: '请选择时间', trigger: 'change'}
                    ],
                    type: [
                        {type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change'}
                    ],
                    resource: [
                        {required: true, message: '请选择活动资源', trigger: 'change'}
                    ],
                    desc: [
                        {required: true, message: '请填写活动形式', trigger: 'blur'}
                    ]
                },
            }
        },
        methods: {
            findSize(size) {
                this.size = size;
                this.findAllTableData(this.page, size);
            },
            // 用来处理分页的相关方法
            findPage(page) {
                this.page = page
                this.findAllTableData(page, this.size);
            },
            // 点击添加时的清空信息处理
            saveUserInfo() {
                this.show = true;
                this.form = {sex: '男'};
            },
            handleEdit(index, row) {
                console.log(index, row);
                // 展示编辑表单
                this.show = true;
                // 双向绑定，填充回显信息
                this.form = row;
            },
            handleDelete(index, row) {
                console.log(index, row);
                // 发送axios请求处理删除
                this.$http.get("http://localhost:8989/user/delete?id=" + row.id).then(res => {
                    if (res.data.status) {
                        this.$message({
                            message: res.data.msg,
                            type: 'success'
                        });
                        // 刷新数据
                        this.findAllTableData();
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            },
            onSubmit(userForm) {
                // 表单验证
                this.$refs[userForm].validate((valid) => {
                    if (valid) {
                        // 发送一个ajax请求
                        this.$http.post("http://localhost:8989/user/saveOrUpdate", this.form).then(res => {
                            console.log(res.data);
                            if (res.data.status) {
                                this.$message({
                                    message: '恭喜你，' + res.data.msg,
                                    type: 'success'
                                });
                                //清空表单信息
                                this.form = {sex: '男'};
                                //隐藏表单
                                this.show = false;
                                //调用刷新数据的方法
                                this.findAllTableData();
                            } else {
                                this.$message.error('错了哦，这是一条错误消息');
                            }
                        },)
                    } else {
                        this.$message.error('当前输入的数据不合法');
                        return false;
                    }
                });

            },
            findAllTableData(page, size) {
                page = page?page:this.pageNow,
                    size = size?size:this.size,
                this.$http.get("http://localhost:8989/user/findByPage?pageNow="+page+"&pageSize="+size).then(res => {
                    this.tableData = res.data.users;
                    this.total = res.data.total;
                })
            }
        },
        created() {
            this.findAllTableData();
        }
    }
</script>

<style scoped>
    .transition-box {
        margin-bottom: 10px;
        width: 100%;
        height: 600px;
        border-radius: 4px;
        padding: 40px 20px;
        box-sizing: border-box;
        margin-right: 20px;
    }
</style>



